@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'sass:color';

.paginacion {
    margin-top: 5rem;
    margin-bottom: 3rem;
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    &__enlace {
        color: v.$azul;
        font-size: 1.5rem;
        font-weight: v.$bold;

        &--texto {
            padding: 1rem;
            border-radius: .5rem;
            border: 1px solid v.$azul;

            &:hover {
                cursor: pointer;
                background-color: v.$azul;
                color: v.$blanco;
            }
        }

        &--numero {
            font-weight: v.$bold;
            display: none;

            @include m.tablet {
                display: inline;
            }
        }

        &--actual {
            color: v.$gris-oscuro;
            font-weight: v.$bold;
            display: none;

            @include m.tablet {
                display: inline;
            }
        }
    }
}