@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'sass:color';

.tabla {
    width: 100%;
border-collapse: collapse;
    &__thead {
        background-color: v.$gris-oscuro;
        color: v.$blanco;
    
    }

    &__th{
        padding: 1rem;
        text-align: left;
    
    }

    &__tr {
        &:nth-child(odd) {
            background-color: v.$gris-claro;
        }
    }

    &__td {
        padding: 1.5rem 1rem;

        &--acciones {
            @extend .tabla__td;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            gap: 2rem;

        }
    }

    &__accion {
        all: unset;
        text-transform: uppercase;
        font-size: 1.4rem;
        font-weight: v.$bold;

        &--editar {
            color: v.$azul;

            &:hover {
                cursor: pointer;
            }
        }

        &--eliminar {
            color: v.$rojo;
            &:hover {
                cursor: pointer;
            }
        }
    }
}